<template>
	<view class="num-box">
		<view class="iconfont iconjian" @tap='reduce'></view>
		<view class="num">
			{{num}}
		</view>
		<view class="iconfont iconjia" @tap='add'></view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				one_num:1
			}
		},
		props:{
			num:{
				default:1,
				type:Number
			}
		},
		methods: {
			reduce() {
				let one_num=this.num
				if (one_num > 1) {
					one_num--;
					this.$emit('getNum', one_num)
				}
			},
			add() {
				let one_num=this.num
				one_num++;
				this.$emit('getNum', one_num)
			}
		}
	}
</script>

<style lang="scss">
	.num-box {
		justify-content: space-between;
		align-items: center;

		.num {
			float: left;
			font-size: 26rpx;
			padding: 0 10rpx;
			min-width: 50rpx;
			text-align: center;
			height: 38rpx;
			line-height: 38rpx;
		}

		.iconfont {
			width: 38rpx;
			height: 38rpx;
			line-height: 38rpx;
			background: #EEEEEE;
			border-radius: 100%;
			float: left;
			color: #333;
			font-size: 20rpx;
			text-align: center;
			cursor: pointer;
			cursor: pointer;

		}
	}
</style>
